}

.project-title{
    text-align: center;
    font-family: fantasy;
    font-variant: small-caps;
}

.card {
    display: inline-flexbox;
    /* display: flex; */
    /* flex-wrap: wrap; */
    /* flex-direction: row; */
    margin: 1rem 2rem;
    justify-content: space-between;
    align-items: center;

    background-color: white;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    transition: 0.3s;
    /* border: 5px solid #2b2f77; */
    cursor: pointer;
    text-align: center;
   
    border-radius: 15px;
    background: #eeeeee;
    padding: 2rem;
    

    font-family: 'Imbue', serif;
    /* font-family: Arial, Helvetica, sans-serif; */
}
